<template>
    <section class="organizationtype">
        LISTE

        <transition name="fade">
            <div class="error overlay" v-if="error">
                <div class="overlay-content">
                    <i class="icon-warning-empty"></i>
                    {{ error }}
                    <br>
                    <a href="#" @click="error = null" class="btn btn-sm btn-default btn-xs">
                        <i class="icon-cancel-circled"></i>
                        Fermer</a>
                </div>
            </div>
        </transition>

        <transition name="fade">
            <div class="pending overlay" v-if="pendingMsg">
                <div class="overlay-content">
                    <i class="icon-spinner animate-spin"></i>
                    {{ pendingMsg }}
                </div>
            </div>
        </transition>

        <transition name="fade">
            <div class="overlay" v-if="formData">
                FORM
            </div>
        </transition>



        <nav class="text-right">
            <a href="#" @click.prevent="handlerNew" v-show="creatable" class="oscar-link">
                <i class="icon-calendar-plus-o"></i>
                Nouveau type d'organisation
            </a>
        </nav>

    </section>

</template>
<script>
    //////////////////////////////////////////////////////////////
    import MilestoneItem from './MilestoneItem.vue'
    import Datepicker from './Datepicker.vue'

    export default {
        props: ['url'],

        components: {

        },

        data() {
            return {
                organizationtypes: [],
                error: null,
                formData: null,
                loading: false,
                creatable: false
            }
        },

        computed: {

        },

        methods: {
            /**
             * Chargement des jalons depuis l'API
             */
            getMilestones() {
                this.pendingMsg = "Chargement des jalons : " + this.url;

                this.$http.get(this.url).then(
                    success => {
                       console.log("SUCCESS");
                    },
                    error => {
                        this.error = "Impossible de charger les types d'oganisations : " + error
                    }
                ).then(n => { this.loading = ""; });
            }
        },

        mounted() {
            this.getMilestones()
        }
    }
</script>


